html,body {
  margin: 0;
  padding: 0;
  min-width: 1300px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
::-webkit-scrollbar{
    width: 6px;
    height: 6px;
    background-color: #F5F5F5;
}
::-webkit-scrollbar-track{
    -webkit-box-shadow: 0 0 3px rgba(0,0,0,0.3) inset;
    background-color: #F5F5F5;
}
::-webkit-scrollbar-thumb{
    border-radius: 3px;
    -webkit-box-shadow: 0 0 3px rgba(0,0,0,0.3) inset;
    background-color: #999;
}
::-webkit-scrollbar-thumb:hover{
    background-color: #777;
}
::-webkit-scrollbar-thumb:active{
    background-color: #555;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body{
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
p,li,ul,a{
    margin: 0;
    padding: 0;
}
li{
    list-style: none;
}
a,a:hover,a:visited,a:link,a:active,a:focus{
    text-decoration: none;
}

.content{
    height:calc(100vh - 40px);
    min-height: 475px;
}
.personal{
    width: 25%;
}
.wrap{
    display: block;
    height: 40px;
    font-size: 24px;
    width: 170px;
    margin: auto;
    cursor: pointer;
}
.wrap:hover{
    color: white;
}
.title-content{
    color: rgba(255, 255, 255, 0.65);
    background-color: rgba(0, 0, 0, 0.05)
}
.nav{
    width:20%;
    height: 100%;
}
.nav-ul{
    height: 100%;
    font-family: "\6977\4F53";
    font-weight: bold;
    margin-bottom: 0;
}
.nav-ul li{
    position: relative;
    top:60px;
    font-size: 20px;
    color: rgb(179, 224, 241);
    text-align: center;
    padding: 5px;
    border-bottom: 2px solid transparent;
    margin-top: 10px;
    transition: 0.5s
}
.nav-ul li:nth-child(1){
    margin-top: 0;
}
.nav-ul li:hover{
    background-color: rgba(26, 134, 177,.2);
}
.nav-ul li:hover a{
    color: white;
}
.nav-ul li::after{
    position: absolute;
    bottom:-2px;
    left: 50%;
    content: '';
    width: 0;
    height: 2px;
    transition: 0.5s;
}
.nav-ul li:hover::after{
    position: absolute;
    width: 100%;
    left: 0;
}
.nav-ul .on{
    font-size: 32px;
}
.nav-ul .on a{
    color:white;
}
.nav-ul li a{
    display: block;
}
.bodyhead{
    height: 42px;
    line-height: 42px;
    color: grey;
    font-size: 18px;
    background-color: #00c0ff5e;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow: hidden;
}
.content-a{
    display: block;
    position: relative;
    margin-bottom:10px;
    padding-left:5px;
    color:black;
    font-size: 20px;
    border: 2px solid #6cb1ce;
    border-radius: 10px;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow: hidden;
}
/* hover背景改变 */
.content-a:hover{
    -webkit-transform: scale(0.999);
            transform: scale(0.999);
    transition: .5s;
    color: rgb(164, 28, 28);
    box-shadow: 5px 5px 5px rgba(54, 73, 115, 0.55);
}
/* 阿里图标颜色 */
.content-dir::before{
    color: blue;
}
.content-file::before{
    color: orange;
}

.content-file:hover button{
    right: 3%;
}


.welcome{
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: url(/static/media/book.bf1bce51.png) no-repeat center/cover;
    padding: 150px 0 0 100px;

}
.welcome .wc1,.welcome .wc2{
    width: 160px;
    -webkit-text-stroke: 1px rgb(95, 176, 207);  
    -webkit-text-fill-color : transparent;
    font-size: 40px;
    line-height: 60px;
}
.welcome .wc1{
    white-space:nowrap;
    opacity: 0;
    -webkit-animation: fade-in 3s ease 0s 1 forwards;
            animation: fade-in 3s ease 0s 1 forwards;
}
.welcome .wc1 span{
    -webkit-filter:invert(1);
            filter:invert(1);
}
.welcome .wc2{
    opacity: 0;
    -webkit-animation: fade-in 3s ease 2s 1 forwards;
            animation: fade-in 3s ease 2s 1 forwards;
}
@-webkit-keyframes fade-in
{
    0%{ opacity: 0;}
    100%{opacity:1;}
}
@keyframes fade-in
{
    0%{ opacity: 0;}
    100%{opacity:1;}
}
.welcomes{
    width: 100%;
    height: 100%;
    background-image: url(/static/media/welcome.19f7b45f.gif);
    background-repeat: no-repeat;
    background-position-x: 50%;
    background-size: auto 100%;
    padding: 150px 0 0 100px;
}
.welcomes p{
    margin: 0;
    width: 160px;
    white-space: nowrap;
    -webkit-text-stroke: 1px rgb(252, 232, 118);  
    -webkit-text-fill-color : transparent;
    font-size: 40px;
    line-height: 60px;
}
.uploadbox{
    position: fixed;
    left: calc(20vw + 10px);
    bottom: 10px;
}
.respond{
    position: relative;
    height: 100%;
    overflow-y: auto;
    padding: 25px;
    background-color:#ccd9ee75;
    border: #bbd4ef 1px solid;
    border-bottom-right-radius: 6px;
    border-bottom-left-radius: 6px;
    box-shadow: 0 3px 9px rgba(0,0,0,0.4);
    _zoom: 1;
}
.respond dl{
    padding-bottom: 20px;
    cursor: default;
    zoom: 1;
}
.respond dl dt{
    padding-left: 5px;
    height: 27px;
    color: rgb(41, 116, 182);
    font: bold 14px/28px 宋体;
    background: rgb(148, 176, 218);
    border-bottom: 2px solid rgb(212, 226, 244);
}
.respond dl dt em{
    display: inline-block;
    _display: inline;
    _zoom: 1;
    height: 20px;
    width: 16px;
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    line-height: 18px;
    color: rgb(41, 116, 182);
    position: relative;
    top: 1px;
    z-index: 0;
}
.respond dl dd{
    padding: 6px 30px;
    font-size: 14px;
    list-style: none;
    display: block;
    -webkit-margin-start: 20px;
            margin-inline-start: 20px;
}
.respond dl dd .t{
    /* font-size: 16px; */
    margin: 5px 5px 10px -2em;
}
.respond dl dd div{
    text-indent: 2em;
    margin: 5px 0;
}
.respond img{
    position: relative;
    width: 650px;
    height: 300px;
    margin: 5px 0;
    cursor: pointer;
    left: 14px;
}
.mkdirbox{
    position: fixed;
    left: calc(20vw + 10px);
    bottom: 10px;
}
.conBody{
    width: 55%;
    overflow: visible;
    box-shadow: 0 3px 2px rgba(0,0,0,0.4);
}
.icon{
    border-radius: 50%;
    background-color: white;
    margin: 0 4px;
    cursor: pointer;
}
.personalitemstitle{
    text-align: center;
    cursor: pointer;
    transition: .5s;
    width: 70%;
    border-radius: 52px;
    margin-bottom: 1em;
    font-family: "\6977\4F53";
    font-weight: bold;
    font-size: 20px;
}
.personalitemstitle:hover{
    background-color: #0017ff66;
}
.personalitems{
    margin-bottom: 50px;
    position: relative;
}
.reset{
    position: absolute;
    right: 10%;
    top: 20%;
    cursor: pointer;
    border-radius: 5px;
    padding: 0 5px;
    transition: .5s;
}
.submiticon{
    position: absolute;
    right: 10%;
    bottom: 5%;
    cursor: pointer;
    border-radius: 5px;
    padding: 0 5px;
    transition: .5s;
}
.bgshower{
    width: 180px;
    height: 95px;
    background: linear-gradient( 100deg,
    rgb(55, 74, 171) 0%,
    rgb(52, 119, 158) 20%,
    rgb(57, 187, 218) 30%,
    rgb(27, 67, 140) 40%, 
    rgb(16, 127, 138) 50%,
    rgb(234, 185, 42) 80%,
    rgb(218, 131, 40) 100% );
    cursor: pointer;
}

.changepassword{
    display: flex;
    font-family: "\6977\4F53";
    flex-direction: column;
    align-content: space-between;
}
.changepassword>div{
    display:flex;
    justify-content: space-around;
    margin: 10px;
}
.changepassword>div>div {
    width: 75%;
}
.changepassword button{
    margin-right: 20px;
}
.pwdtitle{
    text-align: center;
    font-size: 32px;
}
.filespath{
    position: relative;
    padding-left: 100px;
    margin: 20px;
    font-family: "\6977\4F53";
    font-size: 24px;
    text-overflow:ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.filespathbutton{
    position: absolute;
    left: 2px;
    top: 0;
    bottom: 0;
    margin: auto;
}
.feedback{
    min-height: 125px;
}
.feedbackmsg{
    display: flex;
    height: 100%;;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content:space-around; 
}
.feedbackmsg i{
    position: absolute;
    left: -27px;
    top: 3px;
}
.feedbackmsg>div{
    display: flex;
    position: relative;
    width: 30%;
}
#feedbacktextbox{
    margin: 42px 0;
    width: 80%;
}
#submitfeedback{
    display: block;
    margin: auto;
}
.feedbackinput{
    background-color: rgba(255,255,255,0.9);
}
.reg{
    display: flex;
    align-content: space-around;
    flex-direction: column;
    font-family: 楷体;
}
.reg>div{
    display: flex;
    justify-content: space-around;
    margin: 10px 0;
}
.reg>div>div,.reg>div>input{
    width: 50%;
    height: 40px;
    line-height: 40px;
}
.userstate{
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    padding: 5px 0 0 0;
}
.historybox{
    height: calc(100% - 53px);
    width: 100%;
}
.historybox dl{
    margin-bottom: 0;
}
.historybox dl dt,.historybox dl dd{
    text-align: center;
    font-size: 20px;
}
.historybox dl dd a{
    display: block;
    color: rgba(255, 249, 0, 0.88);
    border-radius: 20px 0 0 20px;
    padding: 5px 50px;
    transition: .5s;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.historybox dl dd:hover a{
    /* background-color: rgb(209, 101, 101); */
    /* background-color: rgba(124, 101, 209, 0.2); */
    background-color:  rgba(216, 15, 25,0.15);
    -webkit-animation: tran-in 2.5s ease 0s 1;
            animation: tran-in 2.5s ease 0s 1;

}
@-webkit-keyframes tran-in{ 
    0%   { background-color:rgba(216, 15, 25,0.15)  }
    10%  { background-color:rgba(236, 13, 106,0.15) }
    20%  { background-color:rgba(216, 15, 206,0.15) }
    30%  { background-color:rgba(102, 15, 216,0.15) }
    40%  { background-color:rgba(15, 102, 216,0.15) }
    50%  { background-color:rgba(15, 216, 149,0.15) }
    60%  { background-color:rgba(15, 216, 42,0.15)  }
    70%  { background-color:rgba(122, 216, 15,0.15) }
    80%  { background-color:rgba(220, 235, 10,0.15) }
    90%  { background-color:rgba(216, 152, 15,0.15) }
    100% { background-color:rgba(216, 15, 25,0.15)  }
}
@keyframes tran-in{ 
    0%   { background-color:rgba(216, 15, 25,0.15)  }
    10%  { background-color:rgba(236, 13, 106,0.15) }
    20%  { background-color:rgba(216, 15, 206,0.15) }
    30%  { background-color:rgba(102, 15, 216,0.15) }
    40%  { background-color:rgba(15, 102, 216,0.15) }
    50%  { background-color:rgba(15, 216, 149,0.15) }
    60%  { background-color:rgba(15, 216, 42,0.15)  }
    70%  { background-color:rgba(122, 216, 15,0.15) }
    80%  { background-color:rgba(220, 235, 10,0.15) }
    90%  { background-color:rgba(216, 152, 15,0.15) }
    100% { background-color:rgba(216, 15, 25,0.15)  }
}

.content{
    display: flex;
    flex-wrap: nowrap;
}
